import './styles/index.scss';
import 'jquery';
import 'owl.carousel/dist/assets/owl.carousel.min.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import 'owl.carousel';
import 'material-icons/iconfont/material-icons.css';
import './js/pictures.js'
import { pics } from './js/pictures.js';

let tableItems = $('.achievements-table-col-info__item');
let tableCols = $('.achievements-table-col-info');
var owl = $('.owl-carousel');
let table = $('.achievements-table');
let buttons = $('.toggleTable');
let skipDiplom = true;
let nextTimeoutAuto = 3500;
let prevIdx = 100;
let direction = 'next.owl.carousel';

let idxOfDiploms = [0, 11, 13, 15, 16, 17, 19, 21, 24, 25, 27, ];

const top = {
    'memes': $('#memes1')[0],
    'team': $('#team1')[0],
    'achievements': $('#ach1')[0],
    'conclusion': $('#conclusion1')[0]
}

window.onload = () => {
    var bodies = document.getElementsByClassName('team-block-body');
    var blocks = document.getElementsByClassName('team-block-header');
    var items = document.getElementsByClassName('team-block');

    blocks[0].onclick = () => HideBody(0);
    blocks[1].onclick = () => HideBody(1);
    blocks[2].onclick = () => HideBody(2);
    blocks[3].onclick = () => HideBody(3);
    blocks[4].onclick = () => HideBody(4);

    $(document).scroll(() => Visible($('.memes')[0]))
    $(document).scroll(() => Visible($('.team')[0]))
    $(document).scroll(() => Visible($('.achievements')[0]))
    $(document).scroll(() => Visible($('.conclusion')[0]))

    var Visible = function (target) {
        // Все позиции элемента
        var targetPosition = {
            top: window.pageYOffset + target.getBoundingClientRect().top,
            left: window.pageXOffset + target.getBoundingClientRect().left,
            right: window.pageXOffset + target.getBoundingClientRect().right,
            bottom: window.pageYOffset + target.getBoundingClientRect().bottom
          },
          // Получаем позиции окна
          windowPosition = {
            top: window.pageYOffset,
            left: window.pageXOffset,
            right: window.pageXOffset + document.documentElement.clientWidth,
            bottom: window.pageYOffset + document.documentElement.clientHeight
          };
      
        if (targetPosition.bottom > windowPosition.top && // Если позиция нижней части элемента больше позиции верхней чайти окна, то элемент виден сверху
          targetPosition.top < windowPosition.bottom && // Если позиция верхней части элемента меньше позиции нижней чайти окна, то элемент виден снизу
          targetPosition.right > windowPosition.left && // Если позиция правой стороны элемента больше позиции левой части окна, то элемент виден слева
          targetPosition.left < windowPosition.right) { // Если позиция левой стороны элемента меньше позиции правой чайти окна, то элемент виден справа
          // Если элемент полностью видно, то запускаем следующий код
          top[target.classList[0]].style.boxShadow='2px 12px 26px 5px rgba(34, 60, 80, 0.7)'
        } else {
          // Если элемент не видно, то запускаем этот код
          top[target.classList[0]].style.boxShadow='none'
        };
      };

    function HideBody(i) {
        var body = bodies[i];
        var item = items[i];
        if (!body.classList.contains('hidden')) {
            item.classList.toggle('onerow');
            body.classList.toggle('hidden');
            setTimeout(function() {
                body.classList.toggle('notDisplayed');
            }, 300);
        } else {
            item.classList.toggle('onerow');
            body.classList.toggle('notDisplayed');
            setTimeout(function() {
                body.classList.toggle('hidden');
            }, 30);
        }
    }

    setTimeout(function(){
        owl.owlCarousel({
            items: 1, 
            autoWidth: false, 
            autoplay: true,
            autoplayTimeout: nextTimeoutAuto,
            autoplaySpeed: 1000,
            autoplayHoverPause: true,
            loop: true,
            smartSpeed: 500,
            dots: false
        });
    }, 10);

    tableCols[0].onclick = handleClick;
    tableCols[1].onclick = handleClick;
    tableCols[2].onclick = handleClick;

    table[0].onclick = hideShow;

    let currentIdx = 11;

    owl.on('changed.owl.carousel', (e) => {
        currentIdx = e.item.index;
        if (skipDiplom && ~idxOfDiploms.indexOf(currentIdx)) {
            owl.trigger(direction, [0]);
        }
    });
    

    for (let i = 0; i < pics.length; i++) {
        $(`#pic${i+1}`).attr('src', pics[i].img);
    }

    $('#leftarrow')[0].onmouseenter = () => owl.trigger('stop.owl.autoplay');
    $('#rightarrow')[0].onmouseenter = () => owl.trigger('stop.owl.autoplay');

    $('#leftarrow')[0].onmouseleave = () => owl.trigger('play.owl.autoplay', [5000, 1000]);
    $('#rightarrow')[0].onmouseleave = () => owl.trigger('play.owl.autoplay', [5000, 1000]);

    $('#leftarrow')[0].onclick = () => {direction = 'prev.owl.carousel'; owl.trigger('prev.owl.carousel', [300])};
    $('#rightarrow')[0].onclick = () => {direction = 'next.owl.carousel'; owl.trigger('next.owl.carousel')};

    $('.appendingimg-bottom__close')[0].onclick = closePic;

    $('#firstcol')[0].onclick = () => {
        if ($('#asd')[0].style.display != 'none') {
            $('#asd')[0].style.display = 'none';
            $('#doublebutton')[0].style.display = 'inline';
        }
    };

    $('.memes-picsGallery')[0].onclick = (event) =>{
        let idx = findIndex(event.target, $('.memes-picsGallery__img'));
        
        if (~idx) {
            $('#mainpic').attr("src", pics[idx].img);
            $('.appendingimg-bottom__text')[0].innerHTML = pics[idx].text;
            $('.appendingimg')[0].style.display = 'grid';
        }
    };

    $('.memes-buttons__showall')[0].onclick = () => {
        $('.memes-picsGenerator')[0].style.display='none';
        toggleCollection();
    }
    $('.memes-buttons__generate')[0].onclick = () => {
        $('.memes-buttons__showall')[0].classList.add('memes-buttons__hideall');
        toggleCollection();
        generate();
    };

    $('#shapka')[0].onclick = () => {
        console.log(123);
        if (!$('.memes-buttons__showall')[0].classList.contains('memes-buttons__hideall')) {
            toggleCollection();
        }
        $('#mainpic').attr("src", pics[1].img);
        $('.appendingimg-bottom__text')[0].innerHTML = pics[1].text;
        $('.appendingimg')[0].style.display = 'grid';
    }
}

function getRandomInt(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min)) + min;
}

function generate() {
    let idx = getRandomInt(0, pics.length);;
    if (idx === prevIdx)
        if (idx < pics.length - 1)
            idx++;
        else if (idx > 0)
            idx--;

    prevIdx = idx;
    
    $('.memes-picsGenerator')[0].style.display='grid';
    $('#generated-mainpic').attr('src', pics[idx].img);
    $('.appendingimg-bottom__text1')[0].innerHTML = pics[idx].text;
}

function hideShow(event) {
    var index = Object.values(buttons).indexOf(event.target);

    let toHide = $('.tohide');

    if (~index) {
        let col;
        if (index === 0 || index === 1) {
            col = toHide[0];
            if (col.classList.contains('none')) {
                $('#doublebutton')[0].style.display='none';
                $('#asd')[0].style.display='inline';
                handleOpen(col);
                handleOpen(toHide[1]);
            } else {
                $('#doublebutton')[0].style.display='inline';
                $('#asd')[0].style.display='none';
                handleHide(col);
                handleHide(toHide[1]);
            }
        } else {
            col = toHide[1];
            if (col.classList.contains('none')) {
                handleOpen(col);
            } else {
                handleHide(col);
            }
        }
    }

    
}

function handleHide(col) {
    col.classList.add('none');
    col.style.display='none';
}

function handleOpen(col) {
    col.classList.remove('none');
    col.style.display='grid';
}

function handleClick(event) {
    var index = Object.values(tableItems).indexOf(event.target);
    skipDiplom = false;

    if (~index) {

        owl.trigger('to.owl.carousel', [index, 0])
        owl.trigger('stop.owl.autoplay')
        setTimeout(() => {skipDiplom = true; owl.trigger('play.owl.autoplay', [5000, 1000])}, 7000)
        correctHidden();
    }
}

function correctHidden() {
    let toHide = Object.values($('.tohide'));
    toHide.forEach(el => {
        if (!el.classList.contains('none'))
            handleHide(el);
    });
}

function closePic() {
    $('.appendingimg')[0].style.display = 'none';
}

function findIndex(elem, elemClass) {
    var index = Object.values(elemClass).indexOf(elem);

    return index;
}

function toggleCollection()  {
    console.log('hui')
    if (!$('.memes-buttons__showall')[0].classList.contains('memes-buttons__hideall'))
    {
        $('.memes-picsGallery')[0].style.display = 'grid';
        $('.memes-buttons__showall')[0].innerHTML = 'Скрыть коллекцию';
        $('.memes-buttons__showall')[0].classList.add('memes-buttons__hideall');
    }
    else 
    {
        $('.memes-picsGallery')[0].style.display = 'none';
        $('.memes-buttons__hideall')[0].innerHTML = 'Показать всю коллекцию';
        $('.memes-buttons__hideall')[0].classList.remove('memes-buttons__hideall');
    }
}